import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import { Link, graphql } from 'gatsby'
import { LocalesContext } from '../components/locales/LocalesContextProvider'
import SEO from '../components/seo'
import '../styles/blog-tags.css'
import config from'../../gatsby-config'
const { locales } = config.siteMetadata

const Categories = ({ pageContext, data }) => {

  const { langKey, i18n } = useContext(LocalesContext)
  const i18nData = i18n.find(el => el.langKey === langKey)
  const { post: posti18n, allCategories: i18nAllCategories } = i18nData?.locales

  const { category } = pageContext
  const { edges, totalCount } = data.allMdx
  const categoryHeader = `${totalCount} ${posti18n}${
    totalCount === 1 ? "" : "s"
  }`

  return (
    <>
    <SEO
    title={category}
    description={categoryHeader}
    lang={langKey}
    pageId={category}
    template='tags'
  />

    <div id="content" className="container">
      <h1 className="fw" data-text={category}>{category}</h1>
      <div className="fbreak"/>
      <h2>{categoryHeader}</h2>
      <div className="fbreak"/>
      <div className="content">
        <ul>
          {edges.map(({ node }) => {
            const { slug } = node.fields
            const { title } = node.frontmatter
            return (
              <li key={slug}>
                <Link to={slug}>{title}</Link>
              </li>
            )
          })}
        </ul>
      </div>
      <aside id="sidebar">
        <Link to={ langKey === locales.defaultLangKey ? '/categories/' : `/${langKey}/categories`}>{i18nAllCategories}</Link>
      </aside>
    </div>
    </>
  )
}

Categories.propTypes = {
  pageContext: PropTypes.shape({
    category: PropTypes.string.isRequired,
  }),
  data: PropTypes.shape({
    allMdx: PropTypes.shape({
      totalCount: PropTypes.number.isRequired,
      edges: PropTypes.arrayOf(
        PropTypes.shape({
          node: PropTypes.shape({
            frontmatter: PropTypes.shape({
              title: PropTypes.string.isRequired,
            }),
            fields: PropTypes.shape({
              slug: PropTypes.string.isRequired,
            }),
          }),
        }).isRequired
      ),
    }),
  }),
}

export default Categories

/**
 * Get all categories for current page language.
 * 
 * GRAPHQL:
 * 
 * allMdx(
 *  limit: 2000,
 *  sort: {fields: [frontmatter___date], order: DESC},
 *  filter: {
 *    frontmatter: {category: {in: [$category]}},
 *    fields: {langKey: {eq: $langKey}}
 *  }
 * ) {
 * ...
 * }
 */
export const pageQuery = graphql`
  query($category: String, $langKey: String) {
    allMdx(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC },
      filter: { 
        frontmatter: {category: {in: [$category]}},
        fields: {langKey: {in: [$langKey]}}
      }
    ) {
      totalCount
      edges {
        node {
          fields {
            slug
            langKey
          }
          frontmatter {
            title
          }
        }
      }
    }
  }
`